"use client";

import { useState } from 'react';
import FetchWeather, {WeatherData} from '../FetchWeather/FetchWeather';

const DisplayWeather = () => {
  const [weatherData, setWeatherData] = useState<WeatherData | null>(null);

  return (
    <div>
      <FetchWeather onDataLoaded={(data) => setWeatherData(data)} />
      
      {weatherData && (
        <div>
          <h2>{weatherData.location.name}</h2>
          <p>Температура: {weatherData.current.temp_c}°C</p>
          <p>Ощущается как: {weatherData.current.feelslike_c}°C</p>
          <img 
            src={weatherData.current.condition.icon} 
            alt={weatherData.current.condition.text} 
          />
        </div>
      )}
    </div>
  );
};

export default DisplayWeather;